.H_background_poster>img{
    position: absolute;
    z-index: -10;
    width: 100%;
}

.H_background_black{
    background-color: rgba(0,0,0,0.7);
    color: #fff;
    height: 2000px;
}
.H_movie_top{
    position: relative;
}
.H_black{
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    height: 550px;
    transform: translateX(-50%);
    background-image: radial-gradient( rgba(255,255,255,0), rgba(0,0,0,1) 90%);
    z-index: 1;
}
.H_movie_detail{
    text-align: center;
    margin: 0 auto;
}
.H_detail{
    position: relative;
}

.H_detail_poster{
    position: absolute;
    top: -80px;
    left: 30px;
    width: 25%;
    z-index: 2;
    box-shadow: 5px 5px 3px rgba(255,255,255,0.3),
                5px -5px 3px rgba(255,255,255,0.3),
                -5px 5px 3px rgba(255,255,255,0.3),
                -5px -5px 3px rgba(255,255,255,0.3);
}
.H_detail_poster:hover{
    width: 50%;
    left: -15%;
}
.H_movie_detail_text_box{
    margin-left: 35%;
    margin-right: 15%;
}
.H_nav_gnb{
    width: 70%;
    margin: 150px auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
}
.H_nav_gnb>li{
    width: 100%;
}
.H_nav_gnb>li>button{
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    width: 100%;
    padding: 10px;
    text-align: center;
    color: #fff;
    border: none;
    background-color: rgba(0,0,0,0);
    border-bottom: 2px solid rgba(0,200,200,0.5);
}
li>button:hover{
    border:1px solid rgba(0,200,200,0.5)
}
li>button.active{
    box-shadow: 0px 0px 10px rgba(0,200,200,1);
}
.H_nav_lnb{
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
}

.H_information{
    white-space: pre-line;
}
.H_information>h1{
    line-height: 40px;
    text-shadow: 0px 0px 10px rgba(0,200,200,1),
                 2px 0px 1px #000, -2px 0px 1px #000,
                 0px 2px 1px #000, 0px -2px 1px #000;
}
.H_information>h4{
    word-spacing: 1px;
    line-height: 20px;
}

.H_Movie_Page_Slider{
    width: 50%;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-bottom: 50px;
    margin-top: 30px;
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 200, 200, 1);
}
.H_review_total{
    text-align: center;
}
.H_review{
    width: 100%;
    height: 100px;
    padding: 1%;
    margin: 3% 0;
    border: 1px solid #00c8c8;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.H_review_point{
    width: 15%;
    height: 80px;
    text-align: center;
    font-size: 26px;
    line-height: 80px;
    background-color: rgba(255,255,255,0.1);
    border: 1px solid #00c8c8;
}
.H_review_text{
    width: 80%;
    height: 80px;
    text-align: center;
    font-size: 22px;
    line-height: 80px;
    background-color: rgba(255,255,255,0.1);
    border: 1px solid #00c8c8;    
}
.H_bar{
    width: 100%;
    height: 5px;
    border-bottom: 5px dotted rgba(0,200,200,0.5);
}

.H_nav_lnb .swiper-button-next,
.H_nav_lnb .swiper-button-prev{
    color: #00c8c8 !important;
}